import React from 'react';
import { Box, AppBar, Toolbar, Typography, Button, Container, Grid, Avatar, Link } from '@mui/material';
import { styled } from '@mui/material/styles';

// Define theme colors to match the image
const primaryColor = '#BE3144'; // Main Brand Color
const secondaryBackgroundColor = '#292323';
const footerBackgroundColor = '#1a1616';
const textColor = '#ffffff'; // Default Text Color


const LimitLessWax = () => {
  const handleNavigation = (path: string): void => {
    window.location.href = path;
  };
  return (

    <Container maxWidth="md" sx={{ mt: 5, textAlign: 'center' }}>
      <Box sx={{ bgcolor: '#4A1E2A', display: 'flex', flexDirection: 'column', alignContent: 'center', mb: { xs: 4, md: 10 } }}>
        <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', gap: 2 }}>
          <Button
            variant='outlined'
            key={'/login'}
            sx={{ bgcolor: '#882140' }}
            onClick={() => handleNavigation('/login')}
          >
            {'Add Action +'}
          </Button>
          <span>or</span>
          <Button
            variant='outlined'
            key={'/login'}
            sx={{ bgcolor: '#882140' }}
            onClick={() => handleNavigation('/login')}
          >
            {'Login'}
          </Button>
        </Box>



      </Box>
    </Container >


  );
};

export default LimitLessWax;