import React from 'react';
import { Box, Button, TextField, Typography } from '@mui/material';

const Tab4 = () => {
  return (
    <Box sx={{ display: 'flex' }}>
      <Box>
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
          {/* Amount to Send */}
          <Box>
            <TextField
              fullWidth
              label="Amount to send"
              variant="outlined"
              InputLabelProps={{ style: { color: "#fff" } }}
              InputProps={{
                style: {
                  backgroundColor: "#fff",
                  borderRadius: 4,
                },
              }}
            />
          </Box>

          {/* Number of Days */}
          <Box>
            <TextField
              fullWidth
              label="Number of days"
              variant="outlined"
              InputLabelProps={{ style: { color: "#fff" } }}
              InputProps={{
                style: {
                  backgroundColor: "#fff",
                  borderRadius: 4,
                },
              }}
            />
          </Box>

          {/* Amount to be Staked */}
          <Box>
            <Typography
              sx={{ color: "#fff", fontSize: "0.9rem", marginBottom: 1 }}
            >
              Amount to be staked:
            </Typography>
            <Typography
              sx={{
                color: "#fff",
                fontWeight: "bold",
                fontSize: "1rem",
                marginBottom: 2,
              }}
            >
              11.349123158 WAX
            </Typography>
          </Box>

          {/* Request Button */}
          <Box>
            <Button
              fullWidth
              variant="contained"
              sx={{
                backgroundColor: "#d32f2f",
                "&:hover": { backgroundColor: "#b71c1c" },
                color: "#fff",
                fontWeight: "bold",
                textTransform: "none",
                borderRadius: 4,
              }}
            >
              Request self stake
            </Button>
          </Box>
        </Box>
      </Box>

      {/* ROI Section */}
      <Box
        sx={{
          marginTop: 3,
          paddingY: 2,
          paddingX: 3,
          backgroundColor: "#7a1e34",
          borderRadius: 2,
        }}
      >
        <Typography
          sx={{ color: "#fff", fontWeight: "bold", marginBottom: 1 }}
        >
          ROI
        </Typography>
        {["Daily", "Weekly", "Monthly"].map((period, index) => (
          <Typography key={index} sx={{ color: "#fff" }}>
            {period}:{" "}
            <span style={{ fontWeight: "bold" }}>11.349 WAX</span>
          </Typography>
        ))}
      </Box>
    </Box>
  )
}

export default Tab4;